import{extend,padZero,makeArrayProp,clamp}from'../utils';import{pickerSharedProps}from'../picker/Picker';export const sharedProps=extend({},pickerSharedProps,{modelValue:makeArrayProp(),filter:Function,formatter:{type:Function,default:(type,option)=>option}});export const pickerInheritKeys=Object.keys(pickerSharedProps);export function times(n,iteratee){if(n<0){return[];}const result=Array(n);let index=-1;while(++index<n){result[index]=iteratee(index);}return result;}export const getMonthEndDay=(year,month)=>32-new Date(year,month-1,32).getDate();export const genOptions=(min,max,type,formatter,filter,values)=>{const options=times(max-min+1,index=>{const value=padZero(min+index);return formatter(type,{text:value,value});});return filter?filter(type,options,values):options;};export const formatValueRange=(values,columns)=>values.map((value,index)=>{const column=columns[index];if(column.length){const minValue=+column[0].value;const maxValue=+column[column.length-1].value;return padZero(clamp(+value,minValue,maxValue));}return value;});